.carouselImage {
    height: 100%;
    width: 100%;
    border: white 4px solid;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5%;
}
.carouselBlock {
    border: white 6px solid;
    background: rgba(44, 44, 38, 0.719);
    position: absolute;
    top: 450%;
    left: 50%;
    margin-top: 35%;
    /* margin-left: 20px;
    margin-right: 20px; */

    /* bring your own prefixes */
    transform: translate(-50%, -50%);
}
.portfolioHeader {
    font-size: 175px;
    text-align: center;
}
.left {
    background-color: black;
    height: 70px;
    width: 30px;
    position: fixed;
    top: 55%;
}
.right {
    background-color: black;
    height: 70px;
    width: 30px;
    position: fixed;
    top: 55%;
}
.caption {
    color: rgb(243, 242, 242);
    background: rgba(44, 44, 38, 0.719);
    position: absolute;
    top: 60%;
}
.indicators {
    margin-top: 5%;
}

@keyframes rainbow {
    from {
        color: #2828f0;
      }
      10% {
        color: #0099ff;
      }
      50% {
        color: #00ff00;
      }
      75% {
        color: #ff002b;
      }
      100% {
        color: #fac124;
      }
      0%,100% {
        background-position: 0 0;
    }
        50% {
        background-position: 100% 0;
    }
}

/*Media Queries*/

/*Xs devices (portrait phones, less than 576px)*/
@media (max-width: 575px) {
    .portfolioHeader {
        font-size: 60px;
        margin-top: 5%;
    }
    .careerText {
        font-size: 18px;
    }
    .carouselBlock {
        top: 390%
    }
    /* .aboutMeLogo {
        margin-top: 18px;
        margin-right: 14px;
        border: 4px solid rgb(88, 90, 224);
    } */

}
/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767px) {
    .portfolioHeader {
        font-size: 85px;
    }
    .carouselBlock {
        top: 349%;
    }
    /* .aboutMeLogo {
        margin-top: 5%;
        border: 4px solid rgb(88, 90, 224);
    } */
 }

/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991px) {
    .portfolioHeader {
        font-size: 95px;
    }
    .carouselBlock {
        top: 465%;
    }
    /* .aboutMeLogo {
        margin-top: 40px;
        border: 4px solid rgb(88, 90, 224);
    } */
 }

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1999px) {
    .carouselBlock {
        top: 465%;
    }
 }

